
import { defineComponent, reactive } from 'vue';

//参考HP
//https://arrown-blog.com/vuejs-dropdownmenu/
//https://tedate.jp/vue-js/how-to-changing-the-data-part-in-vue3

export default defineComponent({
  name: 'HelloWorld',
  props: {
    msg: String,
  },
  setup(){
    const data = reactive({
      listlabel:["Mens","Womens","Kids"],
      sublistlabel:["Cloth","Pants","Shoes","Goods"],
      active:""
    })

    //method
    //https://tech.synapse.jp/entry/2022/08/15/113000
    function dropdown(list:string) {
        console.log(data.active);
        data.active = data.active===list ? "":list;
    }
    
    return{
      data,
      dropdown
    }
  }
});
